@import "../core/variables.scss";

.landing {
  height: 65vh;
  background: linear-gradient(#05275a, #6b71b9);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: $roboto;
  &__text {
    color: white;
    font-size: 30px;
    margin-top: 30px;
  }
  &__container {
    margin-top: 50px;
    display: flex;
    gap: 25px;
  }
  &__btn {
    font-family: $roboto;
    font-size: 15px;
    color: $mainColor;
    padding: 15px;
    border-radius: 50%;
    cursor: pointer;
    &:hover {
      background-color: $mainColor;
      color: white;
      border: solid 2px white;
      transform: scale(120%);
    }
  }
}
